@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css");
@import url("https://emoji-css.afeld.me/emoji.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css");
@import url("https://cdn.jsdelivr.net/gh/devicons/devicon@v2.15.1/devicon.min.css");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html {
  height: fill-available;
  height: -webkit-fill-available;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  min-height: fill-available;
  min-height: -webkit-fill-available;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
* {
  font-family: "Lato", sans-serif;
}

body {
  background-color: #121212;
  color: #f5f5f5;
}
body header {
  border-bottom: solid #00a0ff 2px;
  padding-bottom: 4rem;
}
body header nav {
  background-color: #2c2c2c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
body header nav .menu {
  display: none;
  background-color: #2c2c2c;
  position: absolute;
  z-index: 80;
  top: 100px;
  left: 0;
  right: 0;
  gap: 1rem;
  margin-inline: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
}
body header nav .menu li {
  list-style: none;
}
body header nav .menu li a {
  text-transform: uppercase;
  text-decoration: none;
  color: #f5f5f5;
  letter-spacing: 3px;
}
body header nav .menu li a:hover {
  transition: all ease-in 0.3s;
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
}
body header nav .menu .contactBtn {
  background: none;
  border: none;
  outline: none;
  background-color: #00a0ff;
  border: solid #00a0ff 3px;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #121212;
  font-weight: 700;
  text-decoration: none;
}
body header nav .menu .contactBtn:hover {
  background-color: #2c2c2c;
  color: #f5f5f5;
  transition: all ease-in 0.2s;
}
body header nav .showMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
body header nav .burgerBtn {
  background: none;
  border: none;
  outline: none;
  color: #00a0ff;
  font-size: 2rem;
  cursor: pointer;
}
@media only screen and (min-width: 1200px) {
  body header nav {
    padding: 1.5rem 1rem;
  }
  body header nav .menu {
    display: flex;
    align-items: center;
    position: relative;
    gap: 1.5rem;
    padding: 0;
    margin: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
  body header nav .burgerBtn {
    display: none;
  }
}
body header .jumbotron {
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  margin-top: 4rem;
}
body header .jumbotron h1 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
}
body header .jumbotron h1 span {
  color: #00a0ff;
}
body header .jumbotron button {
  background: none;
  border: none;
  outline: none;
  background-color: #2c2c2c;
  color: #f5f5f5;
  border-radius: 50%;
  padding: 1rem 1.5rem;
  font-size: 1.2rem;
}
@media only screen and (min-width: 1920px) {
  body header .jumbotron h1 {
    font-size: 3.5rem;
  }
  body header .jumbotron button {
    font-size: 2rem;
    padding: 1rem 2rem;
  }
}
body .container {
  padding: 1rem;
}
body .container .aboutMeMainContainer {
  margin-block: 1rem;
}
body .container .aboutMeMainContainer h2 {
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding-bottom: 1rem;
}
body .container .aboutMeMainContainer .aboutMeContainer {
  display: grid;
  place-items: center;
  margin-top: 1rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .imgContainer {
  padding-inline: 2rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .imgContainer img {
  max-height: 350px;
  border-radius: 0.5rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent {
  background-color: #2c2c2c;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 1rem;
  border-radius: 0.5rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent p {
  font-weight: 300;
  letter-spacing: 3px;
  line-height: 2;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 1rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent p span {
  font-weight: 700;
  color: #00a0ff;
}
body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  border: solid #00a0ff 3px;
  background-color: #00a0ff;
  padding: 0.5rem 1rem;
  margin-top: 2rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700;
  color: #121212;
  border-radius: 0.3rem;
}
body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent button:hover {
  background-color: #2c2c2c;
  color: #f5f5f5;
  transition: all 0.2s ease-in;
}
body .container .skillsMainContainer {
  margin-bottom: 2rem;
}
body .container .skillsMainContainer h2 {
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 2rem;
}
body .container .skillsMainContainer .skillsContainer {
  display: grid;
  place-content: center;
  grid-template-columns: repeat(2, 50%);
  gap: 1rem;
}
body .container .skillsMainContainer .skillsContainer div {
  background-color: #2c2c2c;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  max-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body .container .skillsMainContainer .skillsContainer div h4 {
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
}
body .container .mainProjectsContainer {
  margin-bottom: 2rem;
}
body .container .mainProjectsContainer .projectHeading {
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
  padding-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 3px;
}
body .container .mainProjectsContainer .projectsContainer {
  display: grid;
  place-content: center;
  gap: 1rem;
}
body .container .mainProjectsContainer .projectsContainer div {
  display: grid;
  background-color: #2c2c2c;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  max-height: 2500px;
  padding: 1rem;
  border-radius: 0.5rem;
}
body .container .mainProjectsContainer .projectsContainer div img {
  margin: auto;
}
body .container .mainProjectsContainer .projectsContainer div h2 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  padding-block: 1rem 0.5rem;
  margin-bottom: 1rem;
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
}
body .container .mainProjectsContainer .projectsContainer div p {
  margin-top: 1rem;
  font-weight: 300;
  font-size: 1.2rem;
  letter-spacing: 3px;
  line-height: 2;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 1rem;
}
body .container .mainProjectsContainer .projectsContainer div h4 {
  font-size: 1.2rem;
  font-weight: 700;
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding-block: 1rem;
}
body .container .mainProjectsContainer .projectsContainer div .builtWithContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  gap: 1rem;
  margin-bottom: 1rem;
}
body .container .mainProjectsContainer .projectsContainer div .btnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
body .container .mainProjectsContainer .projectsContainer div .btnContainer a {
  width: 100%;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  border: solid #00a0ff 3px;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 400;
  color: #f5f5f5;
  border-radius: 0.3rem;
  text-decoration: none;
  text-align: center;
}
body .container .mainProjectsContainer .projectsContainer div .btnContainer a:hover {
  background-color: #00a0ff;
  color: #121212;
  transition: all 0.2s ease-in;
}
body .container .contactContainer h2 {
  text-decoration: underline;
  text-decoration-color: #00a0ff;
  text-decoration-thickness: 5px;
  text-underline-offset: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 2rem;
}
body .container .contactContainer .formContainer {
  background-color: #2c2c2c;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
body .container .contactContainer .formContainer form {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 2rem;
}
body .container .contactContainer .formContainer form .formGroup {
  display: grid;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}
body .container .contactContainer .formContainer form .formGroup input {
  border: none;
  outline: none;
  background: none;
  background-color: #2c2c2c;
  color: #f5f5f5;
  letter-spacing: 3px;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
body .container .contactContainer .formContainer form .formGroup input::placeholder {
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 3px;
  color: #f5f5f5;
}
body .container .contactContainer .formContainer form textarea {
  background: none;
  border: none;
  outline: none;
  resize: none;
  background-color: #2c2c2c;
  color: #f5f5f5;
  border-radius: 0.5rem;
  padding: 1rem;
  width: 100%;
  height: 150px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
body .container .contactContainer .formContainer form textarea::placeholder {
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 3px;
  color: #f5f5f5;
}
body .container .contactContainer .formContainer form .recaptchaContainer {
  display: grid;
  place-items: center;
  margin-top: 0.5rem;
}
body .container .contactContainer .formContainer form .btnContainer button {
  width: 100%;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  background-color: #00a0ff;
  border: solid #00a0ff 1px;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700;
  color: #121212;
  border-radius: 0.3rem;
}
body .container .contactContainer .formContainer form .btnContainer button:hover {
  background-color: #2c2c2c;
  color: #f5f5f5;
  transition: all 0.2s ease-in;
}
