  body .container .skillsMainContainer .skillsContainer {
    grid-template-columns: repeat(3, 1fr);
  }
  body .container .contactContainer .formContainer form .formGroup {
    grid-template-columns: repeat(2, 1fr);
  }
  body .container .contactContainer .formContainer form .btnContainer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  body .container .contactContainer .formContainer form .btnContainer button {
    width: 50%;
  }
}
@media only screen and (min-width: 900px) {
  body .container {
    padding: 1rem 2rem;
  }
  body .container .aboutMeMainContainer .aboutMeContainer {
    grid-template-columns: 25% 75%;
    place-items: center;
    margin-block: 2rem;
  }
  body .container .aboutMeMainContainer .aboutMeContainer .imgContainer {
    padding-inline: 0;
  }
  body .container .aboutMeMainContainer .aboutMeContainer .imgContainer img {
    max-height: 850px;
  }
  body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent {
    border-radius: 0.5rem;
    margin-left: 1rem;
  }
  body .container .skillsMainContainer .skillsContainer {
    grid-template-columns: repeat(4, 1fr);
  }
  body .container .mainProjectsContainer .projectsContainer {
    grid-template-columns: repeat(2, 1fr);
  }
  body .container .contactContainer .formContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  body .container .contactContainer .formContainer form {
    width: 90vw;
  }
}
@media only screen and (min-width: 1500px) {
  body .container .aboutMeMainContainer .aboutMeContainer {
    grid-template-columns: 15% 85%;
    place-items: center;
  }
  body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent {
    padding: 2rem;
  }
  body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent p {
    font-size: 1.4rem;
  }
  body .container .skillsMainContainer .skillsContainer {
    grid-template-columns: repeat(6, 1fr);
  }
  body .container .skillsMainContainer .skillsContainer div {
    padding: 2rem;
  }
  body .container .mainProjectsContainer .projectsContainer {
    grid-template-columns: repeat(3, 1fr);
  }
  body .container .mainProjectsContainer .projectsContainer div {
    padding: 2rem;
  }
  body .container .mainProjectsContainer .projectsContainer div p {
    font-size: 1.4rem;
  }
  body .container .contactContainer .formContainer {
    padding: 2rem;
  }
  body .container .contactContainer .formContainer form {
    width: 80vw;
  }
  body .container .contactContainer .formContainer form button {
    width: 50%;
  }
}
@media only screen and (min-width: 3000px) {
  body .container .aboutMeMainContainer .aboutMeContainer .aboutMeContent {
    padding: 4rem;
  }
}
body .footerContainer {
  background-color: #2c2c2c;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}
body .footerContainer .socialLinks ul {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
}
body .footerContainer .socialLinks ul a {
  color: #f5f5f5;
  text-decoration: none;
  padding: 0;
  margin: 0;
  font-size: 1.3rem;
}
body .footerContainer .socialLinks ul a:hover {
  color: #00a0ff;
}
body .footerContainer .copyrightInfo {
  text-align: center;
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: 3px;
}
@media only screen and (min-width: 1500px) {
  body .footerContainer .socialLinks ul {
    gap: 2rem;
  }
  body .footerContainer .socialLinks ul a {
    font-size: 2rem;
  }
}